import React, { useState, useEffect } from 'react';
import _ from 'lodash';
import {
  useLocation,
} from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import {
  Container,
  Grid,
  Typography,
} from '@material-ui/core';
import animation from '../../../lottie/26565-plant-animation.json';
import Lottie from 'lottie-react-web';
import Page from 'src/components/Page';
import Dashboard from './Dashboard';
import firebase from '../../../firebase';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  navbar: {
    paddingBottom: theme.spacing(2),
    paddingTop: theme.spacing(2),
    marginBottom: theme.spacing(2)
  },
  header: {
    textAlign: 'center', padding: theme.spacing(1), paddingBottom: theme.spacing(0)
  },
  tab: {
    paddingLeft: '10px' 
  }
}));

export default function CenteredTabs() {
  const classes = useStyles();
  const { state } = useLocation();
  const [value, setValue] = useState(0);
  const [tabs, setTabs]= useState([]);
  const [lookback, setLookback] = useState(1);

  useEffect(() => {
    let listener = null;

    (async () => {
      if (_.get(state, 'systemId')) {
        listener = firebase
          .firestore()
          .collection('systems')
          .doc(state.systemId)
          .collection('devices')
          .doc('measurements')
          .onSnapshot((snap) => {
            if (snap.exists) {
              const {
                measurements: s,
                lookback: l,
              } = snap.data();

              if (l) {
                setLookback(l);
              }

              setTabs(s);
            }
          });
      }
    })();

    return () => {
      setTabs([]);

      if (listener != null) listener();
      console.log('Unmounting');
    };
  }, [state]);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };


  if (!_.get(state, 'systemId')) {
    return (
      <Page
        className={classes.root}
        title="Dashboard"
      >
        <Typography
          color="textPrimary"
          variant="h3"
          className={classes.header}
        >
          Select or add your license in the menu to the left
        </Typography>
        <Lottie
          width="25%"
          options={{
            animationData: animation
          }}
        />
      </Page>
    );
  }


  return (
    <>
    <Paper className={classes.navbar}>
      <Tabs
        value={value}
        className={classes.tab}
        onChange={handleChange}
        indicatorColor="primary"
        textColor="primary"
        variant="scrollable"
        scrollButtons="auto"
      >
      {tabs.map(tab => <Tab label={tab} key={tab} />)}
      </Tabs>
    </Paper>
    <Page
      className={classes.root}
      title="Dashboard"
    >
      <Container maxWidth={false}>
        <Grid
          container
          spacing={3}
        >
          <Dashboard 
            system={tabs[value]}
            lookback={lookback}
          />
        </Grid>
      </Container>
    </Page>
    </>
  );
}