import 'react-perfect-scrollbar/dist/css/styles.css';
import React, { useEffect, useState } from 'react';
import { useRoutes } from 'react-router-dom';
import { ThemeProvider } from '@material-ui/core';
import GlobalStyles from 'src/components/GlobalStyles';
import 'src/mixins/chartjs';
import theme from 'src/theme';
import routes from 'src/routes';
import firebase from './firebase';

const App = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [loading, setLoading] = useState(true);

  const checkIfUserIsSignedIn = async () => {
    let email = window.localStorage.getItem('emailForSignIn');
    const isPasswordlessSignIn = firebase.auth().isSignInWithEmailLink(window.location.href);

    if (!isPasswordlessSignIn) return false;
    window.localStorage.removeItem('emailForSignIn');

    if (!email) {
      email = window.prompt('Please provide your email for confirmation');
    }

    try {
      const res = await firebase.auth().signInWithEmailLink(email, window.location.href);
      window.history.replaceState(null, 'Dashboard', '/app/dashboard');

      if (res) {
        window.localStorage.removeItem('emailForSignIn');
        return true;
      }

      return false;
    } catch (e) {
      // window.alert('Something went wrong, going back to login page')
      console.log(e);
      return false;
    }
  };

  useEffect(() => {
    (async () => {
      const isSignedIn = await checkIfUserIsSignedIn();
      setIsAuthenticated(isSignedIn);
    })();

    firebase.auth().onAuthStateChanged((user) => {
      if (user != null) {
        setIsAuthenticated(true);
        setLoading(false);
      } else {
        setIsAuthenticated(false);
      }
    });
  }, []);

  const routing = useRoutes(routes(isAuthenticated, loading));

  return (
    <ThemeProvider theme={theme}>
      <GlobalStyles />
      {routing}
    </ThemeProvider>
  );
};

export default App;
