import {
  Button,
  Grid,
  LinearProgress,
  Typography,
  Input,
  Tooltip,
} from "@material-ui/core";
import Divider from "@material-ui/core/Divider";
import { makeStyles } from "@material-ui/core/styles";
import React, { useEffect, useState } from "react";
import WarningIcon from "@material-ui/icons/Warning";
import AnimatedNumber from 'animated-number-react';
import _ from "lodash";
import InputAdornment from "@material-ui/core/InputAdornment";
import firebase from "../../../../firebase";

const useStyles = makeStyles((theme) => ({
  paper: {
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    outline: 0,
    borderRadius: 2,
  },
  buttonContainer: {
    marginTop: theme.spacing(4),
  },
  targetContainer: {
    marginBottom: theme.spacing(4),
  },
}));

const Targets = ({ controls, setSnack, systemId, lastValue }) => {
  const classes = useStyles();
  const [target, setTarget] = useState(0);
  const [disableTarget, setDisableTarget] = useState(true);
  const [activeTarget, setActiveTarget] = useState(false);
  const [ratios, dispatch] = useState({});
  const [loading, setLoading] = useState({
    status: false,
    name: "",
  });

  useEffect(() => {
    setDisableTarget(_.get(_.find(controls), "disableTarget"));
  }, [_.get(_.find(controls), "disableTarget")]);

  useEffect(() => {
    const state = {};

    (async () => {
      setLoading({
        status: true,
        name: "all",
      });

      const key = _.get(_.find(controls), "key");
      const targetRef = firebase
        .firestore()
        .collection("systems")
        .doc(systemId)
        .collection("targets");

      const { docs } = await targetRef
        .where("endTime", ">", new Date() / 1000)
        .where("key", "==", key)
        .get();

      docs.forEach((d) => {
        setTarget(d.data().target);
        setActiveTarget(true);
        state[d.id] = {
          ..._.omit(controls[d.data().name], "id"),
          ...d.data(),
        };
      });

      const isComplete = _.filter(
        controls,
        (c) => !_.some(state, { name: c.name })
      );

      if (isComplete.length > 0) {
        isComplete.forEach((control) => {
          const docRef = firebase
            .firestore()
            .collection("systems")
            .doc(systemId)
            .collection("targets")
            .doc();

          state[docRef.id] = {
            ..._.omit(control, "id"),
            target: control.target || 0,
            ratio: 0,
          };
        });
      }

      setLoading({
        status: false,
        name: "",
      });
    })();

    dispatch(state);
  }, [_.size(controls)]);

  const onSetTarget = (e) => {
    const value = parseFloat(e.target.value);
    Math.abs(setTarget(value));

    if (_.sum(_.map(controls, "binary")) === _.size(controls)) {
      if (lastValue < value) {
        return dispatch(
          _.mapValues(ratios, (o) => ({
            ...o,
            value: o.type === "increase" ? value : 0,
            ratio: o.type === "increase" ? 1 : 0,
          }))
        );
      }

      return dispatch(
        _.mapValues(ratios, (o) => ({
          ...o,
          value: o.type === "decrease" ? value : 0,
          ratio: o.type === "decrease" ? 1 : 0,
        }))
      );
    }
  };

  const getTarget = () => {
    if (loading.status) {
      return (
        <div className={classes.progressContainer}>
          <LinearProgress />
        </div>
      );
    }

    return (
      <>
        <Input
          type="number"
          disabled={disableTarget}
          value={target}
          onChange={onSetTarget}
        />
        {activeTarget && (
          <Tooltip title="This target is currently active">
            <WarningIcon color="primary" />
          </Tooltip>
        )}
      </>
    );
  };

  const renderTargetInput = (control) => {
    if (control.binary)
      return (
        <Typography>
              <AnimatedNumber
                value={Math.round(100 * control.ratio, 2) ||0}
                formatValue={(value) => `${value.toFixed(1)}%`}
                duration={500}
              />
        </Typography>
      );

    return (
      <Input
        type="number"
        disabled={disableTarget || control.binary}
        startAdornment={
          <InputAdornment position="start">
            {`(${Math.round(100 * control.ratio, 2)}%)`}
          </InputAdornment>
        }
        endAdornment={
          <InputAdornment position="end">{control.unit}</InputAdornment>
        }
        inputProps={{ min: 0, style: { textAlign: "center" } }}
        onChange={(e) => {
          const currentTargets = _.sum(
            _.map(ratios, (o, key) =>
              o.name === control.name
                ? parseFloat(e.target.value) || 0
                : o.value || 0
            )
          );

          console.log(currentTargets);

          dispatch(
            _.mapValues(ratios, (o) => ({
              ...o,
              value:
                o.name === control.name
                  ? parseFloat(e.target.value)
                  : o.value || 0,
              ratio:
                currentTargets > 0
                  ? (o.name === control.name
                      ? parseFloat(e.target.value)
                      : o.value || 0) / currentTargets
                  : 0,
            }))
          );
        }}
        value={control.value}
      />
    );
  };

  const getTargetInput = (control, id) => {
    if (
      !id ||
      (loading.status &&
        (loading.name === control.name || loading.name === "all"))
    ) {
      return (
        <div className={classes.progressContainer}>
          <LinearProgress />
        </div>
      );
    }

    return (
      <Grid container justify="space-around">
        {renderTargetInput(control)}
      </Grid>
    );
  };

  const validateRatios = () =>
    Math.abs(_.sumBy(
      _.map(ratios, (val, key) => val || 0),
      "ratio"
    )) - 1 < 0.02;

  const removeTarget = async () => {
    try {
      await Promise.all(
        _.map(ratios, (val, key) => {
          firebase
            .firestore()
            .collection("systems")
            .doc(systemId)
            .collection("targets")
            .doc(key)
            .set(
              {
                endTime: new Date() / 1000,
              },
              { merge: true }
            );
        })
      );

      setSnack({
        open: true,
        message: "Target removed",
        status: "warning",
      });
      setActiveTarget(false);
    } catch (e) {
      setSnack({
        open: true,
        message: "Error: could not remove target...",
        status: "error",
      });
    }
  };

  const uploadTarget = async () => {
    try {
      let today = new Date();
      let tomorrow = new Date();
      tomorrow.setDate(tomorrow.getDate() + 1);

      const isValidRatios = validateRatios();
      if (!isValidRatios) {
        return setSnack({
          open: true,
          message: "Ratios must to sum to 1",
          status: "error",
        });
      }

      await Promise.all(
        _.map(ratios, (val, key) => {
          firebase
            .firestore()
            .collection("systems")
            .doc(systemId)
            .collection("targets")
            .doc(key)
            .set(
              {
                ..._.omit(val, "id"),
                target: parseFloat(target),
                startTime: today / 1000,
                endTime: tomorrow / 1000,
              },
              { merge: true }
            );
        })
      );

      setSnack({
        open: true,
        message: "Success",
        status: "success",
      });
      setActiveTarget(true);
    } catch (e) {
      setSnack({
        open: true,
        message: "Error: could not set target...",
        status: "error",
      });
    }
  };

  return (
    <>
      <Typography variant="h4">Target</Typography>
      <Grid item xs align="center" className={classes.targetContainer}>
        {getTarget()}
      </Grid>
      <Typography variant="h4">Ratio</Typography>
      <Divider style={{ width: "100%" }} />
      <Grid
        container
        className={classes.buttonContainer}
        justify="center"
        alignItems="center"
      >
        {_.map(ratios, (val, key) => (
          <React.Fragment key={val.name}>
            <Grid item xs>
              <Typography>{val.name}</Typography>
            </Grid>
            <Grid item xs align="center">
              {getTargetInput(val, key)}
            </Grid>
            <Divider
              style={{ width: "100%", marginBottom: "3%", marginTop: "3%" }}
            />
          </React.Fragment>
        ))}
        <Grid
          xs={12}
          container
          direction="row"
          justify="space-around"
          alignItems="center"
        >
          <Button
            disabled={disableTarget}
            onClick={uploadTarget}
            fullWidth={!activeTarget}
          >
            {activeTarget ? "Update" : "Save"}
          </Button>
          {activeTarget && (
            <Button
              disabled={disableTarget}
              variant="secondary"
              onClick={removeTarget}
            >
              Remove
            </Button>
          )}
        </Grid>
      </Grid>
    </>
  );
};

export default Targets;
