import React from 'react';
import PropTypes from 'prop-types';
import {
  Card,
  CardMedia,
  CardContent,
  Divider,
  Typography,
  makeStyles,
} from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {},
  title: {
    padding: theme.spacing(2),
  },
  media: {
    height: 0,
    paddingTop: '75%', // 3:4,
    marginTop:'30'
  },
}));

const Image = ({
  className, imageUrl, ...rest
}) => {
  const classes = useStyles();
  const reg = '%2..*%2F(.*?)?alt'

  const getImageName = () => {
    if (imageUrl.match(reg)) {
      return imageUrl.match(reg)[1].split('.')[0]
    }

    return '';
  }  

  return (
    <Card
      title="Title"
      {...rest}
    >
      <Typography 
        className={classes.title} 
        color="textSecondary"
        gutterBottom
        variant="h6"
      >
        {getImageName()}
      </Typography>
      <Divider />
      <CardContent>
        <CardMedia
          className={classes.media}
          image={imageUrl}
        />
      </CardContent>
    </Card>
  );
};

Image.defaultProps = {
  imageUrl: '',
}

Image.propTypes = {
  className: PropTypes.string,
};

export default Image;
