import React, { useState, useEffect, } from 'react';
import { Chart } from 'react-chartjs-2';
import moment from 'moment';
import _ from 'lodash';
import {
  Container,
  LinearProgress,
  Grid,
  makeStyles,
  colors,
} from '@material-ui/core';
import {
  useLocation,
} from 'react-router-dom';
import Page from 'src/components/Page';
import Budget from './Budget';
import Sales from './Sales';
import firebase from '../../../firebase';
import ImageContainer from './ImagesContainer';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3)
  },
  header: {
    textAlign: 'center', padding: theme.spacing(1), paddingBottom: theme.spacing(0)
  }
}));

const formatSeries = (series) => {
  const ratio = Math.ceil(series.length / Math.max(series.length * 0.1, 100));
  const filteredSeries = series.filter((item, index) => (
    (index % ratio === 0) || (index === series.length - 1)));
  
    return Object.keys(_.first(filteredSeries) || {}).sort()
    .filter((o) => o !== 'time').map((key) => ({
      datasets: [
        {
          data: filteredSeries.map((o) => parseFloat(o[key]).toFixed(2)),
          fill: false,
          pointRadius: 0,
          borderColor: colors.indigo[500],
          label: key
        },
      ],
      labels: filteredSeries.map((o) => moment(o.time).format('MMM Do LT'))
    }));
};

const Dashboard = ({
  system,
  lookback,
}) => {
  const classes = useStyles();
  const { state } = useLocation();
  const [measurements, setMeasurements] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    Chart.pluginService.register({
      afterDraw: function(chart, easing) {
        if (chart.tooltip._active && chart.tooltip._active.length) {
          const activePoint = chart.controller.tooltip._active[0];
          const ctx = chart.ctx;
          const x = activePoint.tooltipPosition().x;
          const topY = chart.scales['y-axis-0'].top;
          const bottomY = chart.scales['y-axis-0'].bottom;

          ctx.save();
          ctx.beginPath();
          ctx.moveTo(x, topY);
          ctx.lineTo(x, bottomY);
          ctx.lineWidth = 1;
          ctx.strokeStyle = 'gray';
          ctx.stroke();
          ctx.restore();
        }
      }
    });
  }, []);

  useEffect(() => {
    const today = new Date()
    let listener = null;
    try {
      setLoading(true)
      if (system) {
        listener = firebase
        .firestore()
        .collection('systems')
        .doc(state.systemId)
        .collection('devices')
        .doc('measurements')
        .collection(system)
        .where('end_time', '>', today.getTime() / 1000 - (lookback * 86400))
        .onSnapshot(({ docs }) => {
          setMeasurements(
            formatSeries(
              docs.map((d) => d.data().values).flat()
                  .sort((a, b) => new Date(a.time) - new Date(b.time))
            )
          )
          setLoading(false);
        });
      }
    } catch (e) {
      setLoading(false);
      console.log(e);
    }

    return () => {
      if (listener !== null) listener()
    };
  }, [system]);

  if (loading) return <LinearProgress />;

  return (
    <Page
      className={classes.root}
      title="Dashboard"
    >
      <Container maxWidth={false}>
        <Grid
          container
          spacing={3}
        >
          {measurements.map((series, i) => {
              return (
                <Grid
                  key={_.get(series, 'datasets[0].label')}
                  item
                  lg={4}
                  sm={6}
                  xl={4}
                  xs={12}
                >
                  <Budget
                    title={`${_.get(series, 'datasets[0].label')}`}
                    values={_.get(series, 'datasets[0].data')}
                  />
                </Grid>
              )})
          }
          <ImageContainer system={system} />
          {measurements.map((series, index) => (
              <Grid
                key={_.get(series, 'datasets[0].label')}
                item
                lg={12}
                md={12}
                xl={12}
                xs={12}
              >
                <Sales
                  title={_.get(series, 'datasets[0].label').toUpperCase()}
                  data={series}
                />
              </Grid>
          ))}
        </Grid>
      </Container>
    </Page>
  );
};

export default Dashboard;
