import { Button, LinearProgress, Grid, Typography } from "@material-ui/core";
import Divider from "@material-ui/core/Divider";
import Slider from "@material-ui/core/Slider";
import { makeStyles } from "@material-ui/core/styles";
import axios from "axios";
import React, { useState } from "react";
import { ROOT_URL } from "src/settings";
import firebase from "../../../../firebase";

const useStyles = makeStyles((theme) => ({
  paper: {
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    outline: 0,
    borderRadius: 2,
  },
  buttonContainer: {
    marginTop: theme.spacing(4),
  },
  progressContainer: {
    width: "100%",
  },
}));

const Controls = ({ controls, setSnack }) => {
  const [seconds, setSeconds] = useState(2);
  const [throttle, setThrottle] = useState(0.99);
  const [loading, setLoading] = useState({
    status: false,
    name: "",
  });
  const classes = useStyles();

  const renderSliders = () => (
    <Grid container>
      <Grid item xs>
        <Typography id="discrete-slider" gutterBottom>
          Seconds
        </Typography>
        <Slider
          value={seconds}
          onChange={(v, nw) => setSeconds(nw)}
          valueLabelDisplay="auto"
          valueLabelFormat={(v) => `${v}s`}
          min={0}
          step={0.5}
          max={30}
        />
      </Grid>
      <Grid item xs>
        <Typography id="discrete-slider" gutterBottom>
          Throttle
        </Typography>
        <Slider
          value={throttle}
          onChange={(v, nw) => setThrottle(nw)}
          valueLabelDisplay="auto"
          step={0.01}
          min={0}
          max={1}
        />
      </Grid>
    </Grid>
  );

  const startPump = async (control) => {
    setLoading({
      status: true,
      name: control.name,
    });
    try {
      const token = await firebase.auth().currentUser.getIdToken(true);
      const headers = {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      };

      const { motorId, registryId, deviceId, address } = control;

      const data = {
        motorId,
        deviceId,
        registryId,
        seconds,
        address,
        throttle,
      };

      await axios({
        method: "POST",
        url: `${ROOT_URL}/startMotorUI`,
        data,
        headers,
      });

      setSnack({
        open: true,
        message: "Success",
        status: "success",
      });
    } catch (e) {
      console.log(e);
      setSnack({
        open: true,
        message: "Error: could not start pump...",
        status: "error",
      });
    }
    setLoading({
      status: false,
      name: control.name,
    });
  };

  const getAdjustButtons = (control) => {
    if (loading.status && loading.name === control.name) {
      return (
        <div className={classes.progressContainer}>
          <LinearProgress />
        </div>
      );
    }

    return (
      <Button onClick={() => startPump(control)}>
        <Typography gutterBottom>Start</Typography>
      </Button>
    );
  };

  return (
    <>
      {renderSliders()}
      <Grid
        container
        className={classes.buttonContainer}
        justify="center"
        alignItems="center"
      >
        {controls.map((control) => (
          <React.Fragment key={control.name}>
            <Grid item xs>
              <Typography>{control.name}</Typography>
            </Grid>
            <Grid item xs align="center">
              {getAdjustButtons(control)}
            </Grid>
            <Divider style={{ width: "100%", marginBottom: '3%', marginTop: '3%' }} />
          </React.Fragment>
        ))}
      </Grid>
    </>
  );
};

export default Controls;
