import React, { useState } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Link from '@material-ui/core/Link';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import { version } from '../../../package.json';
import firebase from '../../firebase';
import { baseUrl } from '../../settings';

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright  '}
      <Link style={{ fontFamily: 'Indie Flower' }} color="inherit" href="https://fruitful.ag/">
        fruitful ag
      </Link>
      {' '}
      {new Date().getFullYear()}
      {' '}
      v
      {version}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
  },
  image: {
    backgroundImage: `url(https://source.unsplash.com/user/${Math.random() > 0.5 ? 'erwanhesry' : 'tiagolopes'})`,
    backgroundRepeat: 'no-repeat',
    backgroundColor:
            theme.palette.type === 'light' ? theme.palette.grey[50] : theme.palette.grey[900],
    backgroundSize: 'cover',
    backgroundPosition: 'bottom',
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    color: 'white',
    backgroundColor: '#b8d8be',
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(1, 0, 2),
  }
}));

const SignIn = () => {
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const classes = useStyles();

  const signIn = async () => {
    try {
      localStorage.setItem('emailForSignIn', email);

      await firebase
        .auth()
        .sendSignInLinkToEmail(email, {
          url: `${baseUrl}/app/dashboard`,
          handleCodeInApp: true,
        });

      setMessage('Please check your mail to log in.');
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <div>
      <Snackbar
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={message.length > 0}
        autoHideDuration={6000}
        onClose={() => setMessage('')}
      >
        <Alert onClose={() => setMessage('')} severity="success">
          {message}
        </Alert>
      </Snackbar>
      <Grid container component="main" className={classes.root}>
        <CssBaseline />
        <Grid item xs={false} sm={4} md={7} className={classes.image} />
        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <div className={classes.paper}>
            <Avatar className={classes.avatar}>
              <LockOutlinedIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
              Fruitful
            </Typography>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              color="secondary"
              autoComplete="email"
              autoFocus
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              InputProps={{
                classes: {
                   root: classes.outline
                }
             }}
            />
              <Button
                onClick={() => signIn()}
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                style={{ color: 'white' }}
              >
                Sign In
              </Button>
            <Box mt={2}>
              <Copyright />
            </Box>
          </div>
        </Grid>
      </Grid>
    </div>
  );
};

export default SignIn;
