import { Grid, Typography } from "@material-ui/core";
import Backdrop from "@material-ui/core/Backdrop";
import Divider from "@material-ui/core/Divider";
import Fade from "@material-ui/core/Fade";
import Modal from "@material-ui/core/Modal";
import Box from "@material-ui/core/Box";
import Snackbar from "@material-ui/core/Snackbar";
import { makeStyles } from "@material-ui/core/styles";
import MuiAlert from "@material-ui/lab/Alert";
import React, { useEffect, useReducer, useState } from "react";
import { useLocation } from "react-router-dom";
import firebase from "../../../firebase";
import Controls from "./controlModal/Controls";
import Targets from "./controlModal/Targets";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";

function Alert(props) {
  console.log(props);
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    outline: 0,
    borderRadius: 2,
  },
  buttonContainer: {
    marginTop: theme.spacing(4),
  },
}));

const initialState = {};

function reducer(state, action) {
  switch (action.type) {
    case "set_value":
      return { ...state, [action.payload.prop]: action.payload.value };
    case "reset":
      return initialState;
    default:
      throw state;
  }
}

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <div>{children}</div>
        </Box>
      )}
    </div>
  );
}

const ControlModal = ({ open, setOpen, controls, lastValue }) => {
  const [targets, dispatch] = useReducer(reducer, initialState);
  const [tab, setTab] = useState(0);
  const [snack, setSnack] = useState({
    status: "",
    message: "",
    open: false,
  });
  const { state } = useLocation();
  const classes = useStyles();

  useEffect(() => {
    controls.forEach((c) =>
      dispatch({
        type: "set_value",
        payload: {
          prop: c.name,
        },
      })
    );

    return () => dispatch({ type: "reset" });
  }, []);

  useEffect(() => {
    (async () => {
      if (controls.length > 0) {
        const { docs } = await firebase
          .firestore()
          .collection("systems")
          .doc(state.systemId)
          .collection("targets")
          .where(
            firebase.firestore.FieldPath.documentId(),
            "in",
            controls.map((c) => c.id)
          )
          .get();

        docs.forEach((d) =>
          dispatch({
            type: "set_value",
            payload: {
              prop: d.data().name,
              value: d.data().target,
            },
          })
        );
      }
    })();
  }, [JSON.stringify(controls)]);

  const handleChange = (event, newValue) => {
    setTab(newValue);
  };

  return (
    <div>
      <Snackbar
        open={snack.open}
        autoHideDuration={6000}
        onClose={() =>
          setSnack({
            open: false,
            message: "",
            status: "",
          })
        }
      >
        <Alert
          onClose={() =>
            setSnack({
              open: false,
              message: "",
              status: "",
            })
          }
          severity={snack.status}
        >
          {snack.message}
        </Alert>
      </Snackbar>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={() => setOpen(false)}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <Grid xs={12} md={6} item className={classes.paper}>
            <Typography variant="h3" gutterBottom>
              Control Panel
            </Typography>
            <Tabs
              value={tab}
              onChange={handleChange}
              indicatorColor="primary"
              textColor="primary"
              centered
            >
              <Tab label="Controls" />
              <Tab label="Targets" />
            </Tabs>
            <Divider style={{ marginBottom: 20, marginTop: 20 }} />
            <TabPanel value={tab} index={0}>
              <Controls controls={controls} setSnack={setSnack} />
            </TabPanel>
            <TabPanel value={tab} index={1}>
              <Targets 
                systemId={state.systemId} 
                controls={(controls || []).reduce((obj, item) => ({...obj, [item.name]: item}) , {})} 
                setSnack={setSnack} 
                lastValue={lastValue}
              />
            </TabPanel>
          </Grid>
        </Fade>
      </Modal>
    </div>
  );
};

export default ControlModal;
