import React, { useEffect, useState, } from 'react';
import { useLocation } from 'react-router-dom';
import PropTypes from 'prop-types';
import axios from 'axios';
import _ from 'lodash';
import {
  Avatar,
  Box,
  LinearProgress,
  Divider,
  Drawer,
  Hidden,
  List,
  Typography,
  TextField,
  ListSubheader,
  makeStyles,
  Button
} from '@material-ui/core';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import SnackBar from '../../../components/SnackBar';
import NavItem from './NavItem';
import firebase from '../../../firebase';
import { ROOT_URL } from 'src/settings';

const useStyles = makeStyles(() => ({
  mobileDrawer: {
    width: 256
  },
  desktopDrawer: {
    width: 256,
    top: 64,
    height: 'calc(100% - 64px)'
  },
  avatar: {
    cursor: 'pointer',
    width: 64,
    height: 64
  },
  progress: {
    width: '20%',
  },
  capitalize: {
    textTransform: 'capitalize',
  }
}));

const NavBar = ({ onMobileClose, openMobile }) => {
  const classes = useStyles();
  const location = useLocation();
  const [licenseId, setlicenseId] = useState('');
  const [activatingLicense, setActivatingLicense] = useState(false);
  const [fetchingOrganizations, setfetchingOrganizations] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarStatus, setSnackbarStatus] = useState('info');
  const [organizations, setOrganizations] = useState({});
  const [user, setUser] = useState({});

  const getOrganizations = async () => {
    const { currentUser } = firebase.auth();
    try {
      setfetchingOrganizations(true);
      const { data: { organizations: o } } = await axios.post(`${ROOT_URL}/getUserSystems`, { uid: currentUser.uid });

      setOrganizations(_.keyBy(o, 'name'));
      setfetchingOrganizations(false);
    } catch (e) {
      setfetchingOrganizations(false);
      console.log(e);
    }
  };

  useEffect(() => {
    const { currentUser } = firebase.auth();
    setUser(currentUser);
    getOrganizations();
  }, [_.get(firebase.auth().currentUser, 'email')]);

  const [active, setActive] = React.useState('');

  const handleClick = (name) => {
    setActive((s) => (s === name ? '' : (name || '')));
  };

  useEffect(() => {
    if (openMobile && onMobileClose) {
      onMobileClose();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.pathname]);

  const onAddLicense = async () => {
    try {
      setActivatingLicense(true);
      const { currentUser: { uid } } = firebase.auth();
      const { data } = await axios.post(`${ROOT_URL}/checkLicenseUser`, {
        licenseId,
        uid,
      });

      getOrganizations();
      setSnackbarMessage(data);
      setSnackbarOpen(true);
      setSnackbarStatus('success');
      setActivatingLicense(false);
    } catch ({ response }) {
      setSnackbarMessage(response.data);
      setSnackbarOpen(true);
      setSnackbarStatus('error');
      setActivatingLicense(false);
    }
  };

  const renderOrganizations = () => {
    if (activatingLicense || fetchingOrganizations) {
      return <LinearProgress />;
    }

    return _.map(organizations, (val, key) => (
      <div key={key}>
        <ListItem key={key} button onClick={() => handleClick(key)}>
          <ListItemIcon>
            <Avatar
              src="/static/eco-light.png"
            />
          </ListItemIcon>
          <ListItemText primary={key} />
          {active === key ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={active === key} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            {(val.systems || []).map((s) => (
              <NavItem
                href={`/app/dashboard/${s.id}`}
                systemId={s.id}
                key={s}
                title={s.name}
              />
            ))}
          </List>
        </Collapse>
      </div>
    ));
  };

  const getName = () => {
    const { email } = user;
    if (email) {
      let name = '';

      if (email.includes('@')) {
        name = email.split('@')[0]
      }
  
      if (name.includes('.')) {
        name = name.split('.')[0]
      }
  
      return name;
    }
  }

  const content = (
    <Box
      height="100%"
      display="flex"
      flexDirection="column"
    >
      <Box
        alignItems="center"
        display="flex"
        flexDirection="column"
        p={2}
      >
        <Avatar
          className={classes.avatar}
          src="/static/planet-earth.png"
        />
        <Typography
          className={classes.name}
          color="textPrimary"
          variant="h5"
        >
          Welcome
        </Typography>
        <Typography
          className={classes.capitalize}
          color="textSecondary"
          variant="body2"
        >
          {getName()}
        </Typography>
      </Box>
      <Divider />
      <Box p={2}>
        <List
          component="nav"
          aria-labelledby="nested-list-subheader"
          subheader={(
            <ListSubheader component="div" id="nested-list-subheader">
              Your Organizations
            </ListSubheader>
              )}
          className={classes.root}
        >
          {renderOrganizations()}
        </List>
      </Box>
      <Box flexGrow={1} />
      <Divider />
      <Box
        alignItems="center"
        display="flex"
        flexDirection="row"
        p={2}
      >
        <TextField
          placeholder="Add a License"
          value={licenseId}
          onChange={(e) => setlicenseId(e.target.value)}
        />
        <div className={classes.progress}>
          {activatingLicense
            ? <LinearProgress />
            : <Button onClick={() => onAddLicense()} color="primary">Add</Button>}
        </div>
      </Box>
    </Box>
  );

  return (
    <>
      <div style={{ position: 'absolute' }}>
        <SnackBar
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'left',
          }}
          message={snackbarMessage}
          setOpen={setSnackbarOpen}
          open={snackbarOpen}
          status={snackbarStatus}
        />
      </div>
      <Hidden lgUp>
        <Drawer
          anchor="left"
          classes={{ paper: classes.mobileDrawer }}
          onClose={onMobileClose}
          open={openMobile}
          variant="temporary"
        >
          {content}
        </Drawer>
      </Hidden>
      <Hidden mdDown>
        <Drawer
          anchor="left"
          classes={{ paper: classes.desktopDrawer }}
          open
          variant="persistent"
        >
          {content}
        </Drawer>
      </Hidden>
    </>
  );
};

NavBar.propTypes = {
  onMobileClose: PropTypes.func,
  openMobile: PropTypes.bool
};

NavBar.defaultProps = {
  onMobileClose: () => { },
  openMobile: false
};

export default NavBar;
