import React, { useState, useEffect, } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import _ from 'lodash';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  Typography,
  colors,
  makeStyles,
} from '@material-ui/core';
import {
  useLocation,
} from 'react-router-dom';
import useIsMounted from 'react-is-mounted-hook';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import LaunchIcon from '@material-ui/icons/Launch';
import AnimatedNumber from 'animated-number-react';
import firebase from '../../../firebase';
import ControlsModal from './ControlsModal';
import { hexToRGB } from 'src/settings';

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100%',
    width: '100%',
    '& > * + *': {
      marginTop: theme.spacing(2),
    },
  },
  capitalize: {
    textTransform: 'capitalize',
  },
  sliderContainer: {
    marginRight: 5,
    marginLeft: 5,
  },
  avatar: {
    height: 56,
    width: 56
  },
  progress: {
    width: '100%'
  },
  differenceIconNegative: {
    color: colors.red[900]
  },
  differenceValueNegative: {
    color: colors.red[900],
    marginRight: theme.spacing(1)
  },
  differenceIconPositive: {
    color: colors.green[900]
  },
  differenceValuePositive: {
    color: colors.green[900],
    marginRight: theme.spacing(1)
  },
}));

const Budget = ({
  className, ...rest
}) => {
  const classes = useStyles();
  const { state } = useLocation();
  const isMounted = useIsMounted();
  const [trend, setTrend] = useState(0);
  const [controls, setControls] = useState([]);
  const [showControls, setshowControls] = useState(false);

  useEffect(() => {
    if (isMounted()) setTrend(_.last(rest.values) - _.mean(_.takeRight(rest.values, 10).map(n => parseFloat(n))));
  }, [_.last(rest.values)]);

  useEffect(() => {
    (async () => {
      const { docs } = await firebase
        .firestore()
        .collection('systems')
        .doc(state.systemId)
        .collection('controls')
        .where('key', '>=', rest.title)
        .where('key', '<=', rest.title + '~')
        .get();

        if (isMounted()) {
          setControls(docs.map(d => ({
            id: d.id,
            ...d.data(),
          })))
        }
    })()
  }, [])

  const getAdjustButtons = () => (
    <Grid item>
      <Button onClick={() => setshowControls(true)}>
        <LaunchIcon />
      </Button>
    </Grid>
  );

  const renderAdjustButtons = () => {
    if (_.size(controls) > 0) {
      return (
        <Grid container justify="flex-end">
          {getAdjustButtons()}
        </Grid>
      );
    }

    return <Box />
  };

  return (
    <Card
      className={clsx(classes.root, className)}
      style={{ backgroundColor: trend < 0 ? hexToRGB('#FDD1CA', 0.2) : hexToRGB('#C8DCBD', 0.2) }}
      {...rest}
    >
      <ControlsModal 
        lastValue={_.last(rest.values)}
        controls={controls}
        open={showControls} 
        setOpen={setshowControls}    
      />
      <CardContent>
        <Grid
          container
          justify="space-between"
          spacing={3}
        >
          <Grid xs={12} item>
            <Grid
              container
              justify="space-between"
              spacing={3}
            >
              <Grid xs={6} item>
                <Typography
                  className={classes.capitalize}
                  color="textSecondary"
                  gutterBottom
                  variant="h6"
                >
                  {rest.title.replaceAll('_', ' ')}
                </Typography>
              </Grid>
              <Grid xs={6} item>
                {renderAdjustButtons()}
              </Grid>
            </Grid>
            <Typography
              color="textPrimary"
              variant="h3"
            >
              <AnimatedNumber
                value={_.last(rest.values)}
                formatValue={(value) => value.toFixed(2)}
                duration={2000}
              />
            </Typography>
            <Box
              mt={2}
              display="flex"
              alignItems="center"
              flexDirection="row"
            >
              {trend < 0
                ? <ArrowDownwardIcon className={classes.differenceIconNegative} />
                : <ArrowUpwardIcon className={classes.differenceIconPositive} />}
              <Typography
                className={trend < 0
                  ? classes.differenceValueNegative : classes.differenceValuePositive}
                variant="body2"
              >
                <AnimatedNumber
                  value={parseFloat(trend).toFixed(2)}
                  formatValue={(value) => value.toFixed(2)}
                  duration={3500}
                />
              </Typography>
              <Typography
                color="textSecondary"
                variant="caption"
              >
                <span>In the last </span>
                <span>{_.size(_.takeRight(rest.values, 10)) * 7}</span>
                <span> minutes</span>
              </Typography>
            </Box>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
};

Budget.propTypes = {
  className: PropTypes.string
};

export default Budget;
