export const baseUrl = process.env.NODE_ENV === 'development' ? 'http://localhost:3000' : 'https://fruitful-ag.web.app';
export const ROOT_URL = 'https://europe-west1-fruitful-ag.cloudfunctions.net';

export const hexToRGB = (hex, alpha = 0.3) => {
  const r = parseInt(hex.slice(1, 3), 16);
  const g = parseInt(hex.slice(3, 5), 16);
  const b = parseInt(hex.slice(5, 7), 16);

  if (alpha) {
    return `rgba(${r}, ${g}, ${b}, ${alpha})`;
  }
  return `rgb(${r}, ${g}, ${b})`;
}
