import React, { useState, useEffect } from "react";
import useIsMounted from "react-is-mounted-hook";
import _ from "lodash";
import { Grid, makeStyles } from "@material-ui/core";
import { useLocation } from "react-router-dom";
import Image from "./Image";
import firebase from "../../../firebase";

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    padding: theme.spacing(1),
  },
  header: {
    textAlign: "center",
    padding: theme.spacing(1),
  },
}));

const ImageContainer = ({ system }) => {
  const classes = useStyles();
  const isMounted = useIsMounted();
  const { state } = useLocation();
  const [imageUrls, setimageUrls] = useState([]);

  useEffect(() => {
    (async () => {
      try {
        const systemId = _.get(state, "systemId");
        if (systemId && system && system.includes("camera")) {
          const { items } = await firebase
            .storage()
            .ref(`systems/${systemId}/${system}`)
            .listAll();

          if (isMounted()) {
            setimageUrls(
              await Promise.all(items.map((i) => i.getDownloadURL()))
            );
          }
        }
      } catch (e) {
        console.log(e);
      }
    })();
  }, [system]);

  return (
    <Grid className={classes.root} container spacing={3}>
      {imageUrls.map((i) => (
        <Grid item key={i} lg={6} md={6} xl={6} xs={12}>
          <Image imageUrl={i} />
        </Grid>
      ))}
    </Grid>
  );
};

export default ImageContainer;
